.UsersList {
  padding: 45px 0;
  display: flex;
  flex-wrap: wrap;
  row-gap: 35px;
  column-gap: 20px;
}
.LoadMoreBtn {
  display: block;
  margin: 0 auto;
  padding: 14px 56px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #373737;
  background: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
}
